
const scrollBtn = document.querySelector('.scrollup');

window.onscroll = () => {
  if (window.scrollY > 150) {
    scrollBtn.classList.add('scrollup_show');
  } else if (window.scrollY < 50) {
    scrollBtn.classList.remove('scrollup_show');
  }
}

scrollBtn.onclick = () => {
  window.scrollTo(0, 0);
}
